import React, {useState, useEffect} from 'react';
import {base_url, $http} from "config";
import {NotificationContainer, NotificationManager} from 'react-notifications';
import ContainerHeader from 'components/ContainerHeader/index';
import IntlMessages from 'util/IntlMessages';
import CardBox from 'components/CardBox/index';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import AttachFile from '@material-ui/icons/AttachFile';
import {useHistory} from 'react-router-dom';

const UserSave = (props) => {
    const History = useHistory();
    const user_id = props.location.state ? props.location.state.id : null;
    const customer_id = props.location.state ? props.location.state.customer_id : null;

    const [first_name,setFirstName] = useState("");
    const [last_name, setLastName] = useState("");
    const [job_title, setJobTitle] =  useState("");
    const [profile_pic, setProfilePic] = useState(null);
    const [user_role, setUserRole] = useState("");
    const [contact_number, setContactNumber] = useState("");
    const [status, setStatus] = useState("");
    const [password, setPassword] = useState("");
    const [email_address, setEmailAddress] = useState("");
    const [user_roles, setUserRoles] = useState([
        {id: 1, content: 'Super Admin'},
        {id: 3, content: 'Account Manager'},
    ]);
    const status_rows = ([
        {id: 0, content: 'No'},
        {id: 1, content: 'Yes'}
    ]);

    useEffect(() => {
        if (user_id > 0) {
            $http.get(base_url + "user/userInfo", {params: {id: user_id}})
            .then((response) => {
                if (response.data.user) {
                    setFirstName(response.data.user.first_name);
                    setLastName(response.data.user.last_name);
                    setJobTitle(response.data.user.job_title);
                    setUserRole(response.data.user.user_type);
                    setContactNumber(response.data.user.mobile);
                    setStatus(response.data.user.status);
                    // setPassword(response.data.user.password);
                    setEmailAddress(response.data.user.email);
                }
            })
        }
        if (customer_id > 0) {
            setUserRoles([
                {id: 2, content: 'Admin'},
                {id: 6, content: 'Normal'}
            ])
        }
    }, [user_id]);

    const SaveUser = () => {
        
        const frmData = new FormData();
        frmData.append('id', user_id);
        frmData.append('first_name', first_name);
        frmData.append('last_name', last_name);
        frmData.append('job_title', job_title);
        frmData.append('user_type', user_role);
        frmData.append('mobile', contact_number);
        frmData.append('status', status);
        frmData.append('password', password);
        frmData.append('email', email_address);
        frmData.append('profile_pic', profile_pic);
        if (customer_id) {
            frmData.append('customer_id', customer_id);
        }
        //console.log("formdata: ", ...frmData);

        $http.post(base_url + "user/updateUser", frmData)
        .then((response) => {
            //console.log(response);
            if (response.data.status === "success") {
                History.push('/app/customers/users')
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 5000, () => {
                    alert('callback');
                });
            }
        }).catch((error)=>{
            console.log("error:", error);
        })
    };

    return (
        <div className="app-wrapper">
            <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.customers.company"/>}/>
            <div className="row">
                <CardBox styleName="col-lg-12">
                    <form className="row" autoComplete="off" >
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="first_name"
                                label="First Name"
                                value={first_name}
                                onChange={(event)=>setFirstName(event.target.value)}
                                margin="normal"
                                helperText="Please Enter First Name"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="last_name"
                                label="Last Name"
                                value={last_name}
                                onChange={(event)=>setLastName(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Last Name"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="job_title"
                                label="Job Title"
                                value={job_title}
                                onChange={(event)=>setJobTitle(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Job Title"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12 d-flex align-items-center">
                            <input type="file" id="profile_pic" accept="image/*" 
                                name="profile_pic" style={{display: "none"}}
                                onChange={(event) => {
                                    setProfilePic(event.target.files[0]);
                                }}/>
                            <Button className="attach-file jr-btn text-muted" 
                                variant="contained" onClick={(e)=>{document.getElementById('profile_pic').click()}}>
                                <AttachFile/> Upload User Profile Image
                            </Button>
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="user_role"
                                select
                                label="User Role"
                                value={user_role}
                                onChange={(event)=>setUserRole(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select User Role"
                                margin="normal"
                                fullWidth
                            >
                                {user_roles.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.content}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="contact_number"
                                label="Contact Number"
                                value={contact_number}
                                onChange={(event)=>setContactNumber(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Contact Number"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField
                                id="status"
                                select
                                label="Status"
                                value={status}
                                onChange={(event)=>setStatus(event.target.value)}
                                SelectProps={{}}
                                helperText="Please select Status"
                                margin="normal"
                                fullWidth
                            >
                                {status_rows.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.content}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="password"
                                label="Password"
                                value={password}
                                onChange={(event)=>setPassword(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Password"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-6 col-12">
                            <TextField 
                                id="email_address"
                                label="Email Address"
                                value={email_address}
                                onChange={(event)=>setEmailAddress(event.target.value)}
                                margin="normal"
                                helperText="Please Enter Email Address"
                                fullWidth
                                />
                        </div>
                        <div className="col-md-12 col-12 text-right">
                            <Button variant="contained" color="primary" className="jr-btn"
                                onClick={()=>SaveUser()}
                            >
                                Save
                            </Button>
                        </div>
                    </form>
                </CardBox>
            </div>
            <NotificationContainer/>
        </div>
    );
}

export default UserSave;